import axios from "axios";
import router from '@/router';
import AuthHelper from './helper/AuthHelper';
const url = '/api/weather/';

class WeatherService{
    // Get location
    static async getLocation(searchName){
        if(searchName.length < 3) return;
        let params = {
            params: {
                k: searchName,
            }
        };
        let header = {...AuthHelper.getHeader(), ...params};
        return axios.get(url, header);
    }
    // Get location weather details
    static async getCurrentWeather(params){
        let getUrl = url + 'details'
        let header = {...AuthHelper.getHeader(), ...{params}};
        return axios.get(getUrl, header);
    }

    // Delete User
    static deleteUser(id){
        let header = {...AuthHelper.getHeader()};
        return axios.delete(`${url}${id}`, header);
    }

    // Update User
    static updateUser(id, formData){
        let datas = formData;
        datas['_id'] = id;
        let header = {...AuthHelper.getHeader()};
        return axios.put(url, datas, header);
    }
    

    // Get user
    static async getUser(id){
        let header = {...AuthHelper.getHeader(), validateStatus: function (status) {
            return status < 500;
          }};
        return axios.get(`${url}${id}`, header);
    }
}

export default WeatherService;